drupal-entity[data-align=center] {
    display: block;
}

.align-center {
    text-align: center;
}

figure,
figure[class] {
    display: table !important;
    figcaption {
        display: table-caption;
        caption-side: bottom;
        text-align: center;
    }
}

figure.align-left {
    float: left;
    margin-right: 7%;
}

figure.align-right {
    float: right;
    margin-left: 7%;
}

[data-rotate="left"] .embedded-entity {
    transform: rotate(-7deg);
}

[data-rotate="right"] .embedded-entity {
    transform: rotate(7deg);
}

[data-rotate-caption="1"] {
    &[data-rotate="left"] {
        .embedded-entity {
            transform: rotate(0deg);
        }
        figure {
            transform: rotate(-7deg);
        }
    }
    &[data-rotate="right"] {
        .embedded-entity {
            transform: rotate(0deg);
        }
        figure {
            transform: rotate(7deg);
        }
    }
}